  <div id="team_page" class="team_page">
    <div v-if="global.mode === 0">
      <div class="title_part">
        <div :class="'title_'+global.lang">{{ global.lang_pack.team }}</div>
      </div>

      <div class="team_part">
        <div class="team_img_part">
          <div class="team_img_box">
            <img
              class="team_img"
              src="/static/team/christy.png"
            />
          </div>
          <div
            class="team_text_line"
          ></div>
          <div class="member_name">
            {{ global.lang_pack.christy }}
          </div>
        </div>
        <div>
        </div>
        <div class="team_text_part">
          <div class="team_idea">
            {{ global.lang_pack.christy_idea }}
          </div>
          <div class="team_text_line"
          ></div>
          <div :class="'content_text_'+global.lang">{{ global.lang_pack.christy_intro }}
          </div>
        </div>
      </div>

      <div class="team_part">

        <div class="team_img_part">
          <div class="team_img_box">
            <img
              class="team_img"
              src="/static/team/max.png"
            />
          </div>
          <div
