    <Footer></Footer>
    <Menu></Menu>
    <LangTransfer></LangTransfer>
    <Logo></Logo>

  </div>
</template>

<style lang="css" scoped>
div.team_page {
  background: #f7f7f7;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  overflow-x: hidden;
}

div.team_part {
  margin-top: 100px;
  /*display: flex;*/
  display: grid;
  grid-template-columns: 486.667rem 973.333rem;
  /*如果只用grid，啥也不用，就有手机版的效果！！！！*/
  padding-left: 11.98%;
  padding-right: 11.98%;
}

div.team_img_part {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-row: 1;
  grid-column: 1;
}

div.team_img_box {
  width: 220px;
  height: 220px;
  overflow: hidden;
  border-radius: 100%;
  border: solid 5px #efcd24;
}

img.team_img {
  object-fit: cover;
  width: 220px;
  height: 220px;
  transition: all 0.2s;
  filter: grayscale(100%);
}

img.team_img:hover {
  filter: grayscale(0%);
  transform: scale(1.1);
}

div.member_name {
  font-size: 35px;
  display: inline-block;
}

div.team_text_part {
  grid-row: 1;
  grid-column: 2;
  /*margin-left: 100px;*/
  align-self: flex-start;
}

div.team_idea {
  font-size: 28px;
  font-weight: bold;
}

div.team_text_line {
  margin-top: 20px;
  width: 100px;
  display: inline-block;
  background: #37383d;
  position: relative;
  height:3px;
  transform:scale(1 , 0.5);
}

@media screen and (max-width: 540px) {
  div.team_part {
    margin-top: 100px;
    /*display: flex;*/
    display: grid;
    grid-template-columns: unset;
    /*如果只用grid，啥也不用，就有手机版的效果！！！！*/
    padding-left: unset;
    padding-right: unset;
  }

  div.team_img_part {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    grid-row: 1;
    grid-column: 1;
  }

  div.team_img_box {
    width: 220px;
    height: 220px;
    overflow: hidden;
    border-radius: 100%;
    border: solid 5px #efcd24;
  }

  img.team_img {
    object-fit: cover;
    width: 220px;
    height: 220px;
    transition: unset;
    filter: unset;
  }

  img.team_img:hover {
    filter: unset;
    transform: unset;
  }

  div.member_name {
    font-size: 30px;
    display: inline-block;
  }

  div.team_text_part {
    margin-top: 50px;
    grid-row: 2;
    grid-column: 1;
    /*margin-left: 100px;*/
    align-self: flex-start;
  }

  div.team_idea {
    font-size:18px;
    font-weight: bold;
  }

  div.team_text_line {
    margin-top: 20px;
    width: 100px;
    display: inline-block;
    background: #37383d;
    position: relative;
  }

}


</style>

<script>
import global from "../global";
import anime from "../../../modules/anime";
import Menu from "../../components/menu/Menu";
import LangTransfer from "../../components/lang_transfer/LangTransfer"
import Footer from "../../components/footer/Footer"
import Logo from "../../components/logo/Logo";

//export default
export default {
  name: "Team",
  data() {
    return {
      global: global,
    };
  },
  components: {
    Menu,
    Footer,
    LangTransfer,
    Logo
  },
  beforeCreate() {
    //set documentElement root size and language package
    global.refresh_windows_size();
    global.set_lang_pack();
  },
  created() {
  },
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.refresh_windows_size();
      })();
    };
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
  },
  destroyed() {
  },
  watch: {
  },
  methods: {

  },
};
</script>



